body {
  font-family: sans-serif;
  text-align: center;
}

.main-container{
  display: flex;
  width: 100%;
}

/* overlay with text in the middle and opaque outline */
.container1 {
  width: 500px;
  margin: auto;
  margin-top: 70px;
  position: relative;

  img {
    width: 100%;
    height: auto;
    transition: 0.5s;
  }

  .overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    .text {
      background-color: #4caf50;
      color: white;
      font-size: 16px;
      padding: 16px 32px;
      opacity: 0;
    }
  }

  &:hover img {
    opacity: 0.3;
  }

  &:hover .text{
    opacity: 1;
  }
}

/* Center zoom overlay with text */
.container2 {
  width: 500px;
  margin: auto;
  margin-top: 70px;
  position: relative;

  img {
    width: 100%;
    height: auto;
  }

  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.8);
    overflow: hidden;
    width: 100%;
    height: 100%;
    transform: scale(0);
    transition: 0.3s;
    .text {
      color: #fff;
      font-size: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
    }
  }

  &:hover .overlay {
    transform: scale(1);
  }
}

/* Overlay slide */

.container3 {
  width: 500px;
  margin: auto;
  margin-top: 70px;
  position: relative;

  img {
    width: 100%;
    height: auto;
  }

  .overlay {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0; // top:0;
    width: 100%;
    height: 0%;
    background: #008cba;
    overflow: hidden;
    transition: 0.5s;
    .text {
      color: white;
      font-size: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
    }
  }

  &:hover .overlay {
    height: 100%;
  }
}